@media (min-device-width: 1024px) {
  .will-fade-in {
    visibility: hidden; } }

@media (min-device-width: 1024px) and (min-device-width: 1024px) {
  .will-slide-down {
    transform: translateY(-100px); } }

@media (min-device-width: 1024px) and (min-device-width: 1024px) {
  .will-slide-up {
    transform: translateY(-100px); } }

@media (min-device-width: 1024px) and (min-device-width: 1024px) {
  .will-slide-left {
    transform: translateX(100px); } }

@media (min-device-width: 1024px) and (min-device-width: 1024px) {
  .will-slide-right {
    transform: translateX(-100px); } }

@keyframes pop {
  from {
    transform: scale(1); }
  85% {
    transform: scale(1); }
  87.5% {
    transform: scale(1.1); }
  90% {
    transform: scale(1); }
  95% {
    transform: scale(1); }
  97.5% {
    transform: scale(1.1); }
  100% {
    transform: scale(1); } }

