.header--border-btm {
  padding: 0 0 30px 0;
  position: relative; }
  @media (min-width: 1024px) {
    .header--border-btm {
      padding-right: 80px; } }
  .header--border-btm:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 120px;
    border-bottom: 1px solid #ef4136; }

.header--border-btm--full {
  position: relative; }
  .header--border-btm--full:after {
    content: "";
    display: block;
    border-bottom: 1px solid #ef4136;
    margin: 30px 0; }

.link--lg {
