.blockquote--standard, .rte blockquote,
.module-text .rte blockquote,
.donate-info blockquote,
.module-intro__content p blockquote,
.event__description p blockquote {
  border-left: 1px solid #ef4136;
  color: inherit;
  line-height: 1.5;
  margin-left: 2em;
  padding-left: 20px;
  font-size: 22px;
  font-size: 2.2rem;
  font-family: 'ABC Whyte'; }
  .blockquote--standard:not(:first-child), .rte blockquote:not(:first-child),
  .donate-info blockquote:not(:first-child),
  .module-intro__content p blockquote:not(:first-child),
  .event__description p blockquote:not(:first-child) {
    margin-top: 2em; }
  .blockquote--standard:not(:last-child), .rte blockquote:not(:last-child),
  .donate-info blockquote:not(:last-child),
  .module-intro__content p blockquote:not(:last-child),
  .event__description p blockquote:not(:last-child) {
    margin-bottom: 2em; }
  @media (min-width: 1024px) {
    .blockquote--standard, .rte blockquote,
